"use client";

import { useEffect, useState } from "react";
import { useForm, useFieldArray } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useAuth } from "@/hooks/useAuth";
import { getPatients, type PatientWithId } from "@/utils/firestore/patients";
import { createReport } from "@/utils/firestore/reports";
import { testsData } from "@/lib/constants/tests-data";
import { toast } from "sonner";
import { Timestamp } from "firebase/firestore";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
  CommandList,
} from "@/components/ui/command";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import {
  Check,
  ChevronsUpDown,
  Users,
  Plus,
  X,
  ChevronDown,
} from "lucide-react";
import { cn } from "@/lib/utils";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible";

const testParameterSchema = z.object({
  name: z.string(),
  value: z.union([z.number(), z.string()]).optional(),
});

const testSchema = z.object({
  testId: z.string().min(1, "Please select a test"),
  parameters: z.array(testParameterSchema),
});

const reportSchema = z.object({
  patientName: z.string().min(1, "Patient name is required"),
  patientAge: z.coerce.number().min(0, "Age must be a positive number"),
  patientGender: z.enum(["Male", "Female", "Other"]),
  patientPhone: z.string().min(10, "Phone number is required"),
  patientEmail: z
    .string()
    .email("Invalid email address")
    .optional()
    .or(z.literal("")),
  doctor: z.string().min(1, "Doctor name is required"),
  date: z.date(),
  tests: z.array(testSchema).min(1, "At least one test is required"),
  comments: z.string().optional(),
});

type ReportFormValues = z.infer<typeof reportSchema>;

export default function MakeReportPage() {
  const { user: firebaseUser, loading: authLoading } = useAuth();
  const [patients, setPatients] = useState<PatientWithId[]>([]);
  const [loading, setLoading] = useState(true);
  const [open, setOpen] = useState(false);
  const [selectedPatient, setSelectedPatient] = useState<PatientWithId | null>(
    null
  );
  const [testPopovers, setTestPopovers] = useState<Record<number, boolean>>({});
  const [openCards, setOpenCards] = useState<Record<number, boolean>>({});

  const form = useForm<ReportFormValues>({
    resolver: zodResolver(reportSchema),
    defaultValues: {
      patientName: "",
      patientAge: 0,
      patientGender: "Male",
      patientPhone: "",
      patientEmail: "",
      doctor: "Self",
      date: new Date(),
      tests: [],
      comments: "",
    },
  });

  const { fields, insert, remove } = useFieldArray({
    control: form.control,
    name: "tests",
  });

  useEffect(() => {
    if (!firebaseUser?.uid) {
      setLoading(false);
      return;
    }

    fetchPatients();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [firebaseUser?.uid]);

  const fetchPatients = async () => {
    if (!firebaseUser?.uid) return;

    try {
      setLoading(true);
      const data = await getPatients(firebaseUser.uid);
      setPatients(data);
    } catch (error) {
      console.error("Failed to fetch patients:", error);
      toast.error("Failed to load patients");
    } finally {
      setLoading(false);
    }
  };

  const handlePatientSelect = (patient: PatientWithId) => {
    setSelectedPatient(patient);
    form.setValue("patientName", patient.name);
    form.setValue("patientAge", patient.age);
    form.setValue(
      "patientGender",
      patient.gender as "Male" | "Female" | "Other"
    );
    form.setValue("patientPhone", patient.phone);
    form.setValue("patientEmail", patient.email || "");
    setOpen(false);
    toast.success(`Patient ${patient.name} selected`);
  };

  const handleTestSelect = (testIndex: number, testId: string) => {
    const test = testsData[testId as keyof typeof testsData];
    if (!test) return;

    const parameters = test.parameters.map((param) => ({
      name: param.name,
      value: undefined,
    }));

    form.setValue(`tests.${testIndex}.testId`, testId);
    form.setValue(`tests.${testIndex}.parameters`, parameters);
    setTestPopovers((prev) => ({ ...prev, [testIndex]: false }));
    toast.success(`${test.name} added`);
  };

  const handleAddTest = () => {
    insert(0, {
      testId: "",
      parameters: [],
    });
    // Open the newly added card and shift existing indices
    setOpenCards((prev) => {
      const newCards: Record<number, boolean> = { 0: true };
      Object.entries(prev).forEach(([key, value]) => {
        newCards[Number(key) + 1] = value;
      });
      return newCards;
    });
  };

  const getTestOptions = () => {
    return Object.entries(testsData).map(([key, value]) => ({
      id: key,
      name: value.name,
      category: value.category,
    }));
  };

  const getTestOptionsByCategory = () => {
    const options = getTestOptions();
    const grouped: Record<string, typeof options> = {};
    options.forEach((option) => {
      if (!grouped[option.category]) {
        grouped[option.category] = [];
      }
      grouped[option.category].push(option);
    });
    return grouped;
  };

  const getSelectedTest = (testId: string) => {
    return testsData[testId as keyof typeof testsData];
  };

  const onSubmit = async (values: ReportFormValues) => {
    if (!firebaseUser?.uid) {
      toast.error("User not authenticated");
      return;
    }

    if (!selectedPatient) {
      toast.error("Please select a patient");
      return;
    }

    try {
      // Transform form data to report structure
      const testsDataForReport: Record<
        string,
        {
          name: string;
          category: string;
          parameters: Record<
            string,
            {
              value: number | string;
              unit: string;
              range: string | Record<string, string>;
            }
          >;
        }
      > = {};

      // Process each test
      values.tests.forEach((test) => {
        const testInfo = getSelectedTest(test.testId);
        if (!testInfo) return;

        const parametersData: Record<
          string,
          {
            value: number | string;
            unit: string;
            range: string | { male: string; female: string };
          }
        > = {};

        // Process each parameter
        test.parameters.forEach((param, paramIndex) => {
          const paramDef = testInfo.parameters[paramIndex];
          if (!paramDef) return;

          parametersData[param.name] = {
            value: param.value ?? "",
            unit: paramDef.unit,
            range: paramDef.range as string | Record<string, string>,
          };
        });

        testsDataForReport[test.testId] = {
          name: testInfo.name,
          category: testInfo.category,
          parameters: parametersData,
        };
      });

      // Create report with createdAt timestamp
      await createReport(firebaseUser.uid, selectedPatient.id, {
        date: Timestamp.now(),
        doctor: "Self",
        tests: testsDataForReport,
        comments: "",
      });

      toast.success("Report created successfully");

      // Reset form after successful submission
      form.reset({
        patientName: "",
        patientAge: 0,
        patientGender: "Male",
        patientPhone: "",
        patientEmail: "",
        tests: [],
      });
      setSelectedPatient(null);
      setTestPopovers({});
      setOpenCards({});
    } catch (error) {
      console.error("Failed to create report:", error);
      toast.error("Failed to create report");
    }
  };

  if (authLoading || loading) {
    return (
      <div className="space-y-6">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Make Report</h1>
          <p className="text-muted-foreground">
            Create a new pathology report for a patient
          </p>
        </div>
        <Card>
          <CardHeader>
            <Skeleton className="h-6 w-48" />
            <Skeleton className="h-4 w-96" />
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (!firebaseUser) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] space-y-4">
        <Users className="h-12 w-12 text-muted-foreground" />
        <h2 className="text-2xl font-semibold">Not Authenticated</h2>
        <p className="text-muted-foreground">Please log in to create reports</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold tracking-tight">Make Report</h1>
        <p className="text-muted-foreground">
          Create a new pathology report for a patient
        </p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Patient Information</CardTitle>
          <CardDescription>
            Search and select a patient to populate their details
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
              <FormField
                control={form.control}
                name="patientName"
                render={({ field }) => (
                  <FormItem className="flex flex-col">
                    <FormLabel>Patient</FormLabel>
                    <Popover open={open} onOpenChange={setOpen}>
                      <PopoverTrigger asChild>
                        <FormControl>
                          <Button
                            variant="outline"
                            role="combobox"
                            className={cn(
                              "w-full justify-between",
                              !field.value && "text-muted-foreground"
                            )}
                          >
                            {field.value
                              ? patients.find(
                                  (patient) => patient.name === field.value
                                )?.name || "Select patient..."
                              : "Select patient..."}
                            <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                          </Button>
                        </FormControl>
                      </PopoverTrigger>
                      <PopoverContent className="w-full p-0" align="start">
                        <Command>
                          <CommandInput placeholder="Search patients..." />
                          <CommandList>
                            <CommandEmpty>No patients found.</CommandEmpty>
                            <CommandGroup>
                              {patients.map((patient) => (
                                <CommandItem
                                  key={patient.id}
                                  value={patient.name}
                                  onSelect={() => handlePatientSelect(patient)}
                                >
                                  <Check
                                    className={cn(
                                      "mr-2 h-4 w-4",
                                      selectedPatient?.id === patient.id
                                        ? "opacity-100"
                                        : "opacity-0"
                                    )}
                                  />
                                  <div className="flex flex-col">
                                    <span>{patient.name}</span>
                                    <span className="text-xs text-muted-foreground">
                                      {patient.age} years, {patient.gender} •{" "}
                                      {patient.phone}
                                    </span>
                                  </div>
                                </CommandItem>
                              ))}
                            </CommandGroup>
                          </CommandList>
                        </Command>
                      </PopoverContent>
                    </Popover>
                    <FormDescription>
                      Search for a patient by name to auto-fill their details
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <div className="grid grid-cols-2 gap-4">
                <FormField
                  control={form.control}
                  name="patientName"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Name</FormLabel>
                      <FormControl>
                        <Input
                          placeholder="Patient name"
                          {...field}
                          readOnly
                          className="bg-muted"
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="patientAge"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Age</FormLabel>
                      <FormControl>
                        <Input
                          type="number"
                          placeholder="Age"
                          {...field}
                          onChange={(e) =>
                            field.onChange(Number(e.target.value))
                          }
                          readOnly
                          className="bg-muted"
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <FormField
                  control={form.control}
                  name="patientGender"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Gender</FormLabel>
                      <Select
                        onValueChange={field.onChange}
                        defaultValue={field.value}
                        disabled
                      >
                        <FormControl>
                          <SelectTrigger className="w-full bg-muted">
                            <SelectValue placeholder="Select gender" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          <SelectItem value="Male">Male</SelectItem>
                          <SelectItem value="Female">Female</SelectItem>
                          <SelectItem value="Other">Other</SelectItem>
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="patientPhone"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Phone Number</FormLabel>
                      <FormControl>
                        <Input
                          type="tel"
                          placeholder="Phone number"
                          {...field}
                          readOnly
                          className="bg-muted"
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <FormField
                control={form.control}
                name="patientEmail"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Email</FormLabel>
                    <FormControl>
                      <Input
                        type="email"
                        placeholder="Email address (optional)"
                        {...field}
                        readOnly
                        className="bg-muted"
                      />
                    </FormControl>
                    <FormDescription>Email address (optional)</FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <div className="grid grid-cols-2 gap-4">
                <FormField
                  control={form.control}
                  name="doctor"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Doctor</FormLabel>
                      <FormControl>
                        <Input
                          placeholder="Doctor name"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="date"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Date</FormLabel>
                      <FormControl>
                        <Input
                          type="date"
                          {...field}
                          value={
                            field.value
                              ? new Date(field.value)
                                  .toISOString()
                                  .split("T")[0]
                              : new Date().toISOString().split("T")[0]
                          }
                          onChange={(e) => {
                            field.onChange(new Date(e.target.value));
                          }}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <div className="border-t pt-6">
                <div className="flex items-center justify-between mb-4">
                  <div>
                    <h3 className="text-lg font-semibold">Tests</h3>
                    <p className="text-sm text-muted-foreground">
                      Add one or more tests and enter their parameter values
                    </p>
                  </div>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={handleAddTest}
                  >
                    <Plus className="mr-2 h-4 w-4" />
                    Add Test
                  </Button>
                </div>

                {fields.length === 0 && (
                  <div className="text-center py-8 border border-dashed rounded-lg">
                    <p className="text-sm text-muted-foreground">
                      No tests added. Click "Add Test" to get started.
                    </p>
                  </div>
                )}

                <div className="space-y-6">
                  {fields.map((field, index) => {
                    const selectedTest = form.watch(`tests.${index}.testId`)
                      ? getSelectedTest(form.watch(`tests.${index}.testId`))
                      : null;
                    const parameters =
                      form.watch(`tests.${index}.parameters`) || [];
                    const isOpen = openCards[index] ?? true; // Default to open for new cards

                    return (
                      <Card key={field.id}>
                        <Collapsible
                          open={isOpen}
                          onOpenChange={(open) =>
                            setOpenCards((prev) => ({ ...prev, [index]: open }))
                          }
                        >
                          <CardHeader>
                            <div className="flex items-center justify-between">
                              <CollapsibleTrigger asChild>
                                <Button
                                  variant="ghost"
                                  className="flex items-center gap-2 p-0 h-auto hover:bg-transparent"
                                >
                                  <CardTitle className="text-base">
                                    {selectedTest
                                      ? selectedTest.name
                                      : `Test ${index + 1}`}
                                  </CardTitle>
                                  <ChevronDown
                                    className={cn(
                                      "h-4 w-4 transition-transform",
                                      isOpen && "transform rotate-180"
                                    )}
                                  />
                                </Button>
                              </CollapsibleTrigger>
                              <Button
                                type="button"
                                variant="ghost"
                                size="icon"
                                onClick={() => {
                                  remove(index);
                                  // Reindex remaining cards after removal
                                  setOpenCards((prev) => {
                                    const newCards: Record<number, boolean> =
                                      {};
                                    Object.entries(prev).forEach(
                                      ([key, value]) => {
                                        const keyNum = Number(key);
                                        if (keyNum < index) {
                                          newCards[keyNum] = value;
                                        } else if (keyNum > index) {
                                          newCards[keyNum - 1] = value;
                                        }
                                      }
                                    );
                                    return newCards;
                                  });
                                }}
                              >
                                <X className="h-4 w-4" />
                              </Button>
                            </div>
                          </CardHeader>
                          <CollapsibleContent>
                            <CardContent className="space-y-4">
                              <FormField
                                control={form.control}
                                name={`tests.${index}.testId`}
                                render={({ field }) => (
                                  <FormItem className="flex flex-col">
                                    <FormLabel>Select Test</FormLabel>
                                    <Popover
                                      open={testPopovers[index] || false}
                                      onOpenChange={(open) =>
                                        setTestPopovers((prev) => ({
                                          ...prev,
                                          [index]: open,
                                        }))
                                      }
                                    >
                                      <PopoverTrigger asChild>
                                        <FormControl>
                                          <Button
                                            variant="outline"
                                            role="combobox"
                                            className={cn(
                                              "w-full justify-between",
                                              !field.value &&
                                                "text-muted-foreground"
                                            )}
                                          >
                                            {field.value
                                              ? getSelectedTest(field.value)
                                                  ?.name || "Select test..."
                                              : "Select test..."}
                                            <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                                          </Button>
                                        </FormControl>
                                      </PopoverTrigger>
                                      <PopoverContent
                                        className="w-full p-0"
                                        align="start"
                                      >
                                        <Command>
                                          <CommandInput placeholder="Search tests..." />
                                          <CommandList>
                                            <CommandEmpty>
                                              No tests found.
                                            </CommandEmpty>
                                            {Object.entries(
                                              getTestOptionsByCategory()
                                            ).map(([category, items]) => (
                                              <CommandGroup
                                                key={category}
                                                heading={category}
                                              >
                                                {items.map((test) => (
                                                  <CommandItem
                                                    key={test.id}
                                                    value={`${test.name} ${test.category}`}
                                                    onSelect={() =>
                                                      handleTestSelect(
                                                        index,
                                                        test.id
                                                      )
                                                    }
                                                  >
                                                    <Check
                                                      className={cn(
                                                        "mr-2 h-4 w-4",
                                                        field.value === test.id
                                                          ? "opacity-100"
                                                          : "opacity-0"
                                                      )}
                                                    />
                                                    {test.name}
                                                  </CommandItem>
                                                ))}
                                              </CommandGroup>
                                            ))}
                                          </CommandList>
                                        </Command>
                                      </PopoverContent>
                                    </Popover>
                                    <FormMessage />
                                  </FormItem>
                                )}
                              />

                              {selectedTest && parameters.length > 0 && (
                                <div className="space-y-4 pt-4 border-t">
                                  <FormLabel>Test Parameters</FormLabel>
                                  <div className="space-y-3">
                                    {parameters.map((param, paramIndex) => {
                                      const paramDef =
                                        selectedTest.parameters[paramIndex];

                                      // Get range based on patient gender
                                      let range: string;
                                      if (typeof paramDef.range === "string") {
                                        range = paramDef.range;
                                      } else {
                                        const patientGender =
                                          selectedPatient?.gender?.toLowerCase();
                                        if (
                                          patientGender &&
                                          paramDef.range[
                                            patientGender as keyof typeof paramDef.range
                                          ]
                                        ) {
                                          range = paramDef.range[
                                            patientGender as keyof typeof paramDef.range
                                          ] as string;
                                        } else {
                                          // Fallback to first available range value
                                          range = Object.values(
                                            paramDef.range
                                          )[0] as string;
                                        }
                                      }

                                      return (
                                        <div
                                          key={paramIndex}
                                          className="grid grid-cols-[2fr_1fr_1fr_1fr] gap-4 items-end"
                                        >
                                          <div>
                                            <FormLabel className="text-sm font-normal">
                                              {paramDef.name}
                                            </FormLabel>
                                            <p className="text-xs text-muted-foreground">
                                              Range: {range} {paramDef.unit}
                                            </p>
                                          </div>
                                          <FormField
                                            control={form.control}
                                            name={`tests.${index}.parameters.${paramIndex}.value`}
                                            render={({ field }) => (
                                              <FormItem>
                                                <FormControl>
                                                  <Input
                                                    type="text"
                                                    placeholder="Value"
                                                    {...field}
                                                    value={
                                                      field.value !== undefined
                                                        ? String(field.value)
                                                        : ""
                                                    }
                                                    onChange={(e) => {
                                                      const value =
                                                        e.target.value;
                                                      field.onChange(
                                                        value === ""
                                                          ? undefined
                                                          : value
                                                      );
                                                    }}
                                                  />
                                                </FormControl>
                                                <FormMessage />
                                              </FormItem>
                                            )}
                                          />
                                          <div className="text-sm text-muted-foreground">
                                            {paramDef.unit}
                                          </div>
                                          <div className="text-xs text-muted-foreground">
                                            {range}
                                          </div>
                                        </div>
                                      );
                                    })}
                                  </div>
                                </div>
                              )}
                            </CardContent>
                          </CollapsibleContent>
                        </Collapsible>
                      </Card>
                    );
                  })}
                </div>
              </div>

              <div className="flex justify-end gap-4 pt-4 border-t">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => {
                    form.reset({
                      patientName: "",
                      patientAge: 0,
                      patientGender: "Male",
                      patientPhone: "",
                      patientEmail: "",
                      tests: [],
                    });
                    setSelectedPatient(null);
                    setTestPopovers({});
                    setOpenCards({});
                  }}
                >
                  Clear
                </Button>
                <Button
                  type="submit"
                  disabled={!selectedPatient || fields.length === 0}
                >
                  Create Report
                </Button>
              </div>
            </form>
          </Form>
        </CardContent>
      </Card>
    </div>
  );
}
